<script>
export default {
  props: {
    fields: {
      type: Array,
      required: true,
    },
    header: {
      type: String,
      required: true,
    },
    modulename: {
      type: String,
      required: true,
    },
  },
  data: function () {
    return {
      Fields: this.fields, 
      popover: {
                visibility: 'click'
            },
            modelConfig: {
        type: 'string',
        mask: 'YYYY-MM-DD', 
      },  
    };
  },
  watch: {
    fields: function (value) {
      this.Fields = value != null && value.length > 0 ? value : [];
    },
  },
  created: function () {
    var vm = this;
    // setTimeout(function(){
    //   vm.SetScrolbar('dark-thick','', 'filterscroll_leftsearch')
    // }, 1500);
    // setTimeout(function () {
    //   $(
    //     'span[data-target=".multi-collapse"],a[data-target=".multi-collapse"]'
    //   ).unbind("click");
    //   $(".bottom_filter_button").unbind("click");
    // }, 2000);
  },
 methods: {
  isBlank: function (str) {
         return (!str || /^\s*$/.test(str));
     },
     GetLocalizedValue: function (fieldName, displayName) {
         try {
             if (this.isBlank(displayName)) {
                 var chk = this.$t(String(fieldName).toUpperCase());
                 if (chk != String(fieldName).toUpperCase()) {
                     return chk;
                 }
                 chk = this.$t(String(fieldName));
                 if (chk || /^\s*$/.test(chk) == true) {
                     return chk;
                 }
                 return fieldName;
             }
             else {
                 var chk = this.$t(String(displayName).toUpperCase());
                 if (chk != String(displayName).toUpperCase()) {
                     return chk;
                 }
                 chk = this.$t(String(displayName));
                 if (chk || /^\s*$/.test(chk) == true) {
                     return chk;
                 }
                 return displayName;
             }
         }
         catch (ex) { console.log(ex) }
     },
    ExpandCollapsedAll: function () {  
           // debugger;
            if ($('span[data-target=".multi-collapse"],a[data-target=".multi-collapse"]').hasClass("collapsed")) {
                setTimeout(function () {
                    $(".left-menu-tab .card").each(function () {
                        $(this).find(".card-header h5 a").removeClass("collapsed");
                        $(this).find(".card-header h5 a").attr("aria-expanded", "true");
                        $(this).find(".multi-collapse").collapse('show');
                    });
                }, 500);
            } else {
                setTimeout(function () {
                    $(".left-menu-tab .card").each(function () {
                        $(this).find(".card-header h5 a").addClass("collapsed");
                        $(this).find(".card-header h5 a").attr("aria-expanded", "false");
                        $(this).find(".multi-collapse").collapse('hide');
                    });
                }, 500);

            }
       
      // const nodeList = document.querySelectorAll('span[data-target=".multi-collapse"],a[data-target=".multi-collapse"]');
      // if (Array.from(nodeList).some(e => e.classList.contains("collapsed"))
      // ) {        
      //   setTimeout(function () {
      //     Array.from(document.querySelectorAll('span[data-target=".multi-collapse"],a[data-target=".multi-collapse"]')).forEach(e => e.classList.remove("collapsed"))
      //     Array.from(document.querySelectorAll(".left-menu-tab .card")).forEach(e => function () {
      //       e.find(".card-header h5 a").removeClass("collapsed");
      //       e.find(".card-header h5 a").attr("aria-expanded", "true");
      //       e.find(".multi-collapse").collapse("show");           
      //     })
      //   }, 500);
      // } else {
      //   setTimeout(function () {
      //     Array.from(document.querySelectorAll('span[data-target=".multi-collapse"],a[data-target=".multi-collapse"]')).forEach(e => e.classList.add("collapsed"))
      //     Array.from(document.querySelectorAll(".left-menu-tab .card")).forEach(e => function () {
      //       e.find(".card-header h5 a").addClass("collapsed");
      //       e.find(".card-header h5 a").attr("aria-expanded", "false");
      //       e.find(".multi-collapse").collapse("hide");     
      //     })
      //   }, 500);
      // }
    },
    ClearFilter: function () {
      $.each(this.Fields, function (index, item) {
        item.isSearch = false;
        item.value = "";
        item.secondValue = "";
        if (item.fieldType == "ddl-check") {
          $("#customcheckboxselectall" + index).prop("checked", false);
        }
        $(".tab-Search-btn").css("display", "none");
      });
      $(".searchcustomfilter").val("");
      $(".searchcustomfilter").trigger("keyup");
      this.SearchData();
    },
    CheckValueSelectedForbadge: function (badgeValue, selectedValue, isSearch) {
      var array = selectedValue.split(",");
      if (array.includes(badgeValue) && isSearch == true) {
        return false;
      } else {
        return true;
      }
    },
    ClearFilerForbadge: function (badgeValue, item, fieldIndex) {
      var array = item.value.split(",");
      var index = array.indexOf(badgeValue);
      array.splice(index, 1);
      item.value = array.join(",");
      try {
        if (item.fieldType == "ddl-check") {
          if (item.value.split(",").length == item.listOptions.length) {
            $("#customcheckboxselectall" + fieldIndex).prop("checked", true);
          } else {
            $("#customcheckboxselectall" + fieldIndex).prop("checked", false);
          }
        }
      } catch (e) {}
      this.SearchData();
    },
    UpdateCheckedValues: function (fieldItem, value, event, selectAllId) {
      if (event.target.checked == true) {
        if (fieldItem.value != "") {
          var array = fieldItem.value.split(",");
          array.push(value);
          fieldItem.value = array.join(",");
        } else {
          fieldItem.value = value;
        }
      } else {
        var array = fieldItem.value.split(",");
        var index = array.indexOf(value);
        array.splice(index, 1);
        fieldItem.value = array.join(",");
      }
      try {
        if (fieldItem.value.split(",").length == fieldItem.listOptions.length) {
          $("#" + selectAllId).prop("checked", true);
        } else {
          $("#" + selectAllId).prop("checked", false);
        }
      } catch (e) {}
    },
    CheckIfSelected: function (item, value) {
      var array = item.value.split(",");
      var index = array.indexOf(value);
      return index > -1;
    },
    SetMinMaxValueForDatePicker: function (item, type) {
      if (item.fieldType == "date-range") {
        if (type == "datefrom" && item.value != null && item.value != "") {
          var selectedDate = moment(item.value, item.datePickerConfig.format);
          item.secondDatePickerConfig.minDate = new Date(selectedDate);
        } else if (
          type == "dateto" && item.secondValue != null && item.secondValue != "") {
          var selectedDate = moment(item.secondValue,item.secondDatePickerConfig.format);
          item.datePickerConfig.maxDate = new Date(selectedDate);
        }
        //  else if (type == "datefrom" && item.value == "") {
        //   debugger;
        //   var day = new Date().getDate();
        //   var Year = new Date().getFullYear() - 1;
        //   var month = new Date().getMonth() + 1;
        //   var olddate = moment(String(month + "/" + day + "/" + Year), "MM/DD/YYYY" ).format( 'MM-dd-yyyy');
        //   item.secondDatePickerConfig.minDate = new Date(olddate);
        //   item.datePickerConfig.maxDate = new Date();
        // }
      }
    },
      SearchData: function () {
        debugger;
          var vueObj = this;
          var searchCondition = "";
          if (vueObj.modulename == "CRM" || vueObj.modulename == "HR" || vueObj.modulename == "Expense" || vueObj.modulename == "FSM" || vueObj.modulename == "DocStorage") {
              $.each(vueObj.Fields, function (index, item) {
                
                  switch (item.fieldType) {                  
                      case "text":
                          if (item.value.length > 0 && item.fieldSearchConditionName.length > 0) {
                              searchCondition += $.trim(searchCondition) != "" ? " AND (" : "(";
                              var orCondition = "";
                              var val = item.value.replace(/'/g, "''");
                              item.fieldSearchConditionName.forEach(function (field) {
                                  orCondition += $.trim(orCondition) != "" ? " OR " : "(";
                                  orCondition += " " + field + " LIKE '%" + val + "%'";
                              });
                              var combinedCondition = "";
                              if (item.fieldSearchConditionName.length > 1) {
                                if(vueObj.$route.path=='/CRM/ContactListing'){
                                  combinedCondition = " OR " + item.fieldSearchConditionName.join(" %2B ' ' %2B ") + " LIKE '%" + val + "%'";
                                }else{
                                  combinedCondition = " OR " + item.fieldSearchConditionName.join("+' '+") + " LIKE '%" + val + "%'";
                                }
                              }
                              orCondition += combinedCondition + ")";
                              searchCondition += orCondition + ")";
                              item.isSearch = true;
                          }
                          else {
                              item.isSearch = false;
                          }
                          break;
                      case "ddl-radio":
                      case "radio":
                          if (item.value.length > 0 && item.fieldSearchConditionName.length > 0) {
                              searchCondition += $.trim(searchCondition) != "" ? " AND (" : "(";
                              var orCondition = "";
                              item.fieldSearchConditionName.forEach(function (field) {
                                  orCondition += $.trim(orCondition) != "" ? " OR " : "(";
                                  orCondition += " " + field + " = '" + item.value + "'";
                              });
                              orCondition += ")";
                              searchCondition += orCondition + ")";
                              item.isSearch = true;
                          }
                          else {
                              item.isSearch = false;
                          }
                          break;
                      case "ddl-check":
                      case "checkbox":
                          if (item.value.length > 0 && item.fieldSearchConditionName.length > 0) {
                              searchCondition += $.trim(searchCondition) != "" ? " AND (" : "(";
                              var orCondition = "";
                              var valueArray = item.value.split(",");
                              var finalValues = "'" + valueArray.join("','") + "'";
                              item.fieldSearchConditionName.forEach(function (field) {
                                  orCondition += $.trim(orCondition) != "" ? " OR " : "(";
                                  orCondition += " " + field + " in (" + finalValues + ")";
                              });
                              orCondition += ")";
                              searchCondition += orCondition + ")";
                              item.isSearch = true;
                          }
                          else {
                              item.isSearch = false;
                          }
                          break;
                      case "date":
                          if (item.value != null && item.value.length > 0 && item.fieldSearchConditionName.length > 0) {
                              searchCondition += $.trim(searchCondition) != "" ? " AND (" : "(";
                              var orCondition = "";
                              var selectedDate = moment(
                                  item.value,
                                  item.datePickerConfig.format
                              );
                              var dateWithYYYYMMDD = selectedDate.format("YYYY-MM-DD");
                              item.fieldSearchConditionName.forEach(function (field) {
                                  orCondition += $.trim(orCondition) != "" ? " OR " : "(";
                                  orCondition += " CONVERT(DATE, " + field + ") = '" + dateWithYYYYMMDD + "'";
                              });
                              orCondition += ")";
                              searchCondition += orCondition + ")";
                              item.isSearch = true;
                          }
                          else {
                              item.isSearch = false;
                          }
                          break;
                      case "date-range":
                          if (((item.value != null && item.value.length > 0) || (item.secondValue != null && item.secondValue.length > 0)) && item.fieldSearchConditionName.length > 0) {
                              searchCondition +=
                                  $.trim(searchCondition) != "" ? " AND (" : "(";
                              var orCondition = "";
                              item.fieldSearchConditionName.forEach(function (field) {
                                  orCondition += $.trim(orCondition) != "" ? " OR " : "(";
                                  var splitfield = field.split("|");
                                  var internalCondition = "";
                                  if (item.value != null && item.value.length > 0) {
                                      let selectedDate = moment(
                                          item.value,
                                          item.datePickerConfig.format
                                      );
                                      let dateWithYYYYMMDD = selectedDate.format("YYYY-MM-DD");
                                      internalCondition = " CONVERT(DATE, " + splitfield[0] + ") >= '" + dateWithYYYYMMDD + "'";
                                  }
                                  if (item.secondValue != null && item.secondValue.length > 0) {
                                      let selectedDate = moment(
                                          item.secondValue,
                                          item.secondDatePickerConfig.format
                                      );
                                      let dateWithYYYYMMDD = selectedDate.format("YYYY-MM-DD");
                                      internalCondition += $.trim(internalCondition) != "" ? " AND " : "";
                                      internalCondition += " CONVERT(DATE, " + splitfield[1] + ") <= '" + dateWithYYYYMMDD + "'";
                                  }
                                  orCondition += internalCondition;
                              });
                              orCondition += ")";
                              searchCondition += orCondition + ")";
                              item.isSearch = true;
                          }
                          else {
                              item.isSearch = false;
                          }
                          break;
                  }
              });
          }
          //vueObj.$parent.PageNumber = 1;
          //vueObj.$parent.GroupStart = vueObj.$parent.PageNumber;
          //vueObj.$parent.GroupEnd = vueObj.$parent.PageNumber + 2;
          //vueObj.$parent.conditionForInstantSearch = "";
          //vueObj.$parent.conditionForInstantSearch = searchCondition;
          this.$emit('SetLeftSearch', { 'PageNumber': 1, "searchCondition": searchCondition });
          //vueObj.callbackfunction();
      },
      SearchOnEnter: function (event, itm) {
          if (itm.isNumeric != undefined && itm.isNumeric == true && /[0-9]/.test(event.key) == false && event.key != "Backspace") {
              event.preventDefault();
          }
          if (event.keyCode == 13) {
              this.SearchData();
          }
      },
      SelectAllRecords: function (field, e) {
          if (e.target.checked == true) {
              var selectAll = "";
              field.listOptions.forEach(function (item) {
                  selectAll += selectAll == "" ? item.value : "," + item.value;
              });
              field.value = selectAll;
          } else {
              field.value = "";
          }
      },
  },
}; 
</script>
<template>
  <div>
    <div class="col-md-12 p-0">
    <div class="theme-primary partition">
        <span class="p-name text-white" v-if="header">{{GetLocalizedValue(header)}}</span>
    </div>
  </div>
  <div
    class="accordion md-accordion left-filter mt-2"
    id="accordionEx"
    role="tablist"
    aria-multiselectable="true">
    <div class="card">
      <div class="card-header filter-head" role="tab" id="headingOne1">
          <h5 class="mb-0">
              <em class="fa fa-filter" aria-hidden="true"></em> {{GetLocalizedValue($t("Search")) }}
              <span class="expand_all_filters">
                  <span id="SearchData" v-on:click="SearchData()">
                      <a href="javascript:;"
                         :title="$t('Search')"
                         data-toggle-tooltip="tooltip"
                         class="collapsed">
                          <em aria-hidden="true" class="fa fa-search"></em> 
                      </a>
                  </span>
                  <span v-on:click="ClearFilter()">
                      <a :title="$t('Reset')"
                         href="javascript:;"
                         data-toggle-tooltip="tooltip"
                         class="collapsed">
                          <em aria-hidden="true" class="fa fa-repeat"></em> 
                      </a>
                  </span>

                  <span data-target=".multi-collapse"
                        data-toggle="collapse"
                        v-on:click="ExpandCollapsedAll()"
                        class="collapsed">
                      <a href="javascript:;"
                         :title="$t('ExpandAllCollapseAll')"
                         data-toggle-tooltip="tooltip"
                         class="collapsed">
                          <em aria-hidden="true" class="fa fa-sort-desc"></em> 
                      </a>
                  </span>
              </span>
          </h5>
      </div>
    </div>
    <div class="card-panel">
      <div class="card" v-for="(field, fieldIndex) in Fields" :key="fieldIndex">
        <div class="card-header" role="tab" :id="'headingOnee' + fieldIndex">
          <h5 class="mb-0">
            <a data-toggle="collapse"
              data-parent="#accordionEx"
              :href="'#collapseOne'+fieldIndex"
              aria-expanded="false"
              v-bind:aria-controls="'collapseOne'+fieldIndex"
              class="collapsed">
              <em v-bind:class="'fa ' + field.fieldIcon" aria-hidden="true"></em> 
              <span v-bind:class="{'filter':field.isSearch}">{{GetLocalizedValue(field.fieldName)}}</span>
              <em class="fa fa-angle-down rotate-icon" aria-hidden="true"></em> 
            </a>
            <a
              href="javascript:;"
              v-bind:class="{'tab-Search-btn': true, 'd-block': field.value.length > 0}"
              v-on:click="SearchData()">
              <em class="fa fa-search" aria-hidden="true"></em> 
            </a>
          </h5>
        </div>
        <div
          v-bind:id="'collapseOne'+fieldIndex"
          class="collapse multi-collapse"
          role="tabpanel"
          v-bind:aria-labelledby="'headingOnee' + fieldIndex"
          data-parent="#accordionEx"
          style="">
          <div class="card-body pt-0">
            <div class="form-group" v-if="field.fieldType== 'text'">
              <input
                v-bind:type="(field.isNumeric == true)? 'number':'text'"
                v-model="field.value"
                :name="field.name"
                class="form-control search"
                v-on:keydown="SearchOnEnter($event, field)"
                :placeholder="field.fieldName + ' '"
              />
            </div>
            <div class="form-group"
              v-if="field.fieldType== 'date' || field.fieldType== 'date-range'">
              <div v-bind:class="{'input-group':true, 'mb-2':(field.fieldType == 'date-range') }">
                <v-date-picker :popover="popover" :model-config="modelConfig" :minute-increment="5"  class="input-group" v-model="field.value"   v-bind:name="'DateFrom'+fieldIndex"
                
                  v-bind:change="SetMinMaxValueForDatePicker(field, 'datefrom')" :max-date="field.datePickerConfig.maxDate">
                  <template v-slot="{ inputValue, inputEvents,togglePopover}">
                    <input class="form-control" :value="inputValue" v-on="inputEvents"  :placeholder="(field.fieldType== 'date-range')? $t('DateFrom'):field.fieldName"/>
                    <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em> </span></div>
                  </template>
                </v-date-picker>
              </div>
              <div :class="{'input-group':true, 'disabled':!field.secondDatePickerConfig.minDate}" v-if="field.fieldType== 'date-range'">
                <v-date-picker :popover="popover" :model-config="modelConfig" :minute-increment="5" class="input-group"  v-model="field.secondValue"   v-bind:name="'DateTo'+fieldIndex"
                 v-bind:change="SetMinMaxValueForDatePicker(field, 'dateto')" :min-date="field.secondDatePickerConfig.minDate">
                  <template v-slot="{ inputValue, inputEvents,togglePopover}">
                    <input class="form-control" :value="inputValue" v-on="inputEvents"  :placeholder="$t('DateTo')" />
                    <div @click="togglePopover()" class="input-group-append"><span class="input-group-text"><em class="fa fa-calendar"></em> </span></div>
                  </template>
                </v-date-picker>
              
              </div>
            </div>
            <div class="form-group"  v-if="field.fieldType== 'radio' || field.fieldType== 'checkbox'">
              <div v-bind:class="{'custom-control w-auto custom-control-inline': true, 'custom-radio': field.fieldType== 'radio', 'custom-checkbox': field.fieldType== 'checkbox'}"
                v-for="(item,index) in field.listOptions"  :key="index">
                <input
                  v-if="field.fieldType== 'radio'"
                  type="radio"
                  v-model="field.value"
                  class="custom-control-input dynamic"
                  v-bind:id="'customcheckbox'+fieldIndex+index"
                  name="selectedDealOwner"
                  v-bind:value="item.value"
                />
                <input
                  v-if="field.fieldType== 'checkbox'"
                  v-bind:checked="CheckIfSelected(field, item.value)"
                  :name="field.name"
                  type="checkbox"
                  class="custom-control-input dynamic"
                  v-bind:id="'customcheckbox'+fieldIndex+index"
                  name="selectedDealOwner"
                  v-bind:value="item.value"
                  v-on:change="UpdateCheckedValues(field, item.value, $event)"
                />
                <label
                  class="custom-control-label"
                  v-bind:for="'customcheckbox'+fieldIndex+index"
                >{{item.name}}</label>
              </div>
            </div>
            <div
              class="form-group"
              v-if="field.fieldType== 'ddl-radio' || field.fieldType== 'ddl-check'"
            >
            <vue-scroll>
              <div class="w-100 d-flex flex-wrap filterscroll filterscroll_leftsearch">
                <button
                  type="button"
                  v-bind:class="{'btn search-tag searchtagfloat float-left':true, 'd-none':CheckValueSelectedForbadge(btn.value, field.value, field.isSearch)}"
                  v-for="(btn,index) in field.listOptions" :key="index">
                  <span class="tag-name">{{btn.name}}</span>
                  <span
                    class="badge text-dark"
                    v-on:click="ClearFilerForbadge(btn.value, field, fieldIndex)"
                  >&times;</span>
                </button>
              </div>
            </vue-scroll>
              <div class="w-100">
                <div class="p-0 mb-1 w-100">
                  <div class="input-group customsearchbox">
                    <input
                      type="text"
                      :Placeholder="$t('Search')"
                      :name="field.name"
                      class="form-control searchcustomfilter border-right-0"
                      autocomplete="off"
                    />
                    <div class="input-group-append">
                      <span class="input-group-text bg-transparent bg-white border-left-0">
                        <em class="fa fa-search"></em> 
                      </span>
                    </div>
                  </div>
                </div>
                <vue-scroll>
                <div
                  class="form-group filterscroll filterscroll_leftsearch"
                  style="max-height:200px"
                >
                  <div
                    class="custom-control custom-checkbox custom-control-inline w-100"
                    v-if="field.fieldType == 'ddl-check'"
                  >
                    <input
                      v-bind:id="'customcheckboxselectall'+fieldIndex"
                      type="checkbox"
                      class="custom-control-input dynamic customcheckboxselectall"
                      v-on:change="SelectAllRecords(field, $event)"
                    />
                    <label
                      v-bind:for="'customcheckboxselectall'+fieldIndex"
                      type="checkbox"
                      class="custom-control-label"
                    >Select All</label>
                  </div>
                  <div v-bind:class="{'custom-control custom-checkbox custom-control-inline w-100': true, 'custom-radio': field.fieldType== 'ddl-radio','custom-checkbox': field.fieldType=='ddl-check'}"
                    v-for="(item,index) in field.listOptions" :key="index">
                    <input v-if="field.fieldType== 'ddl-radio'"
                      type="radio"
                      v-model="field.value"
                      class="custom-control-input dynamic"
                      v-bind:id="'customcheckbox'+fieldIndex+index"
                      name="selectedDealOwner"
                      v-bind:value="item.value"
                    />
                    <input
                      v-if="field.fieldType== 'ddl-check'"
                      v-bind:checked="CheckIfSelected(field, item.value)"
                      type="checkbox"
                      class="custom-control-input dynamic"
                      v-bind:id="'customcheckbox'+fieldIndex+index"
                       :name="field.name"
                     
                      v-bind:value="item.value"
                      v-on:change="UpdateCheckedValues(field, item.value, $event, 'customcheckboxselectall'+fieldIndex)"
                    />
                    <label
                      class="custom-control-label"
                      v-bind:for="'customcheckbox'+fieldIndex+index"
                    >{{item.name}}</label>
                  </div>
                </div>
              </vue-scroll>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="bottom_filter_button" @click="ExpandAllCollapseAll">
    <a
      href="javascript:;"
      data-target=".multi-collapse"
      data-toggle="collapse"
      v-on:click="ExpandCollapsedAll"
      class="collapsed">{{$t("ExpandAllCollapseAll")}}</a>
  </div>
  </div>
  

</template>
<style>
.__bar-is-vertical,.__bar-is-horizontal{
  background: rgb(146 146 146) !important;
}
</style>